@use "variables" as v;
@use "mixin" as m;

#about {
  position: relative;
  background-color: #ffffff;
  overflow: hidden;
  padding: 60px 20px;
  color: white;
  height: auto;
  min-height: 100vh; /* ‚ÜêÂøÖË¶Å„Å™„Çâ„Åì„Å°„Çâ */
  .menu-img {
    background-image: url(../img/box/about_img.webp);
    position: relative;
    margin-bottom: 40px;
    width: 100%;
    border-radius: 20px;
  }

  .fixed-bg {
    height: 300px;
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
    @include m.tab {
      height: clamp(150px, 2.5vh, 300px);
    }

    .section-title {
      font-size: clamp(1.4rem, 2.5vw, 3rem); // ‚Üê „É¨„Çπ„Éù„É≥„Ç∑„Éñ„Éï„Ç©„É≥„Éà
      font-weight: bold;
      margin: 0 0 40px 20px;
      color: #ffffff;
      border-bottom: 2px solid #ffffff;
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 10;
      padding: 10px;
    }
  }
}

.feature {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 64px;
  padding: 40px 20px 20px 20px; /* ‰∏ä‰∏ã„ÅÆ‰ΩôÁôΩ„ÇíÂ∫É„Åí„Çã */
  box-sizing: border-box;

  /* ‚úÖ ‰øÆÊ≠£: ËÉåÊôØ„ÅåË°®Á§∫„Åï„Çå„Çã„Çà„ÅÜ„Å´È´ò„Åï„ÇíÊòéÁ¢∫„Å´ÊåáÂÆö */
  height: auto;
  min-height: calc(100vh - 40px); /* ÁîªÈù¢ÂÖ®‰Ωì„ÅÆÈ´ò„Åï„ÇíÁ∂≠ÊåÅ */

  /* ‚úÖ ‰øÆÊ≠£: ËÉåÊôØ„Ç§„É°„Éº„Ç∏„ÅÆÊåáÂÆö */
  //   background-image: radial-gradient(circle, #f0f4ff 10%, transparent 10%), radial-gradient(circle, transparent 10%, #ffffff 10%) !important;
  background-size: 20px 20px !important;
  background-position: 0 0, 10px 10px !important;
  background-repeat: repeat !important;
  z-index: 1;
  position: relative;
  overflow: visible;
  @include m.tab {
    justify-content: flex-start;
    min-height: 0;
    justify-content: center;
    align-items: center;
  }

  li {
    display: inline-block;
	width: 100%;
    max-width: 1000px;
    background-color: #e0f2ff; /* ‚úÖ ‰øÆÊ≠£: ÈÄèÈÅé„Çí„ÇÑ„ÇÅ„Å¶ÂÆåÂÖ®„Å™Ëâ≤ */
    border-left: 4px solid #003366; /* ‚úÖ „Ç¢„ÇØ„Çª„É≥„Éà„Ç´„É©„Éº„ÇÇË™øÊï¥ */
    color: #ffffff; /* ‚úÖ „ÉÜ„Ç≠„Çπ„Éà„Ç´„É©„Éº„ÇÇÁôΩ„ÅßË¶ã„ÇÑ„Åô„Åè */
    padding: 20px;
	margin-left: auto;
	margin-right: auto;
    margin-bottom: 20px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);

    p {
      color: #003366;
      text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.2);
    }
  }

